<template>
    <div class="box">
        <h3>{{title}}<a href="#">更多</a></h3>
        <addLi :topBook="topBook" :url="url" v-if="topBook"></addLi>
        <ul>
            <li v-for="(v,i) in mlData" :key="i" @click="postData(i)">
                <router-link :to="url">
                    <img v-bind:src="v.images.small" >
                    <p>{{v.title|newName}}</p>
                    <div class="rate">
                        <van-rate v-model="v.rating.average" allow-half void-icon="star" void-color="#eee" size="0.1rem" readonly/>
                        {{v.rating.average*2}}
                    </div>
                </router-link>
            </li>
        </ul>
    </div>
</template>
<script>
import eventBus from "@/bus/bus.js"
import addLi from "@/pages/book/addLi/addLi.vue"
export default {
    props:["title","mlData","url","topBook"],
    components:{
        addLi
    },
    filters:{
        newName(val){
            return val.slice(0,6)
        }
    },
    methods:{
        postData(i){
            if(this.url=="/xiangqing"){
                this.axios({
                    url:"/api/movie/xiangqing",
                    method:"get",
                    params:{id:this.mlData[i].id}
                }).then(ok=>{
                    var dataArr = ok.data.data;
                    dataArr[0].rating.average = dataArr[0].rating.average/2;
                    window.localStorage.setItem("id",dataArr[0].id);
                    eventBus.$emit("postData",dataArr)
                });
            }else{
                return false;
            }
        }
    },
}
</script>
<style scoped>
    .box{
        width: 3rem;
        margin: 0 auto;
        font-size: 0.12rem;
    }
    .box h3{
        font-size: 0.14rem;
        color: #333333;
        height: 0.4rem;
        line-height: 0.4rem;
        font-weight: normal;
    }
    .box h3 a{
        color: green;
        font-size: 0.12rem;
        float: right;
        height: 0.4rem;
        line-height: 0.4rem;
        text-decoration: none;
    }
    ul{
        display: flex;
        height: 1.5rem;
        width: 100%;
        overflow-x: auto;
        justify-content: space-between;
    }
    ul li{
        display: flex;
        flex-direction: column;
        width: 0.8rem;
        height: 1.5rem;
        justify-content: flex-start;
        margin-right: 0.05rem;
    }
    ul li img{
        width: 0.8rem;
        height: 1rem;
        margin-bottom: 0.1rem;
    }
    ul li p{
        text-align: center;
        font-size: 0.12rem;
        color: #333333;
    }
    ul li .rate{
        height: 0.2rem;
        width: 100%;
        display: flex;
        font-size: 0.12rem;
        align-items: center;
        color: #333333;
    }
</style>